	module.exports = {"data":{"markdownRemark":{"html":"<p>Today marks the one-year open-source anniversary of React.</p>\n<p>It’s been a crazy ride. 2.3k commits and 1.5k issues and pull requests later, we’re approaching version 1.0 and nearing 7k GitHub stars, with big names such as Khan Academy, New York Times, and Airbnb (and naturally, Facebook and Instagram) using React in production, and many more developers blogging their success stories with it. The <a href=\"/blog/2014/03/28/the-road-to-1.0.html\">roadmap</a> gives a glimpse into the future of the library; exciting stuff lies ahead!</p>\n<p>Every success has its story. React was born out of our frustration at existing solutions for building UIs. When it was first suggested at Facebook, few people thought that functionally re-rendering everything and diffing the results could ever perform well. However, support grew after we built the first implementation and people wrote their first components. When we open-sourced React, the initial reception was <a href=\"https://www.reddit.com/r/programming/comments/1fak87/react_facebooks_latest_javascript_client_library/\">similarly skeptical</a>. It challenges many pre-established conventions and received mostly disapproving first-impressions, intermingled with positive ones that often were votes of confidence in Facebook’s engineering capabilities. On an open, competitive platform such as the web, it’s been hard to convince people to try React. <a href=\"/docs/jsx-in-depth.html\">JSX</a>, in particular, filtered out a huge chunk of potential early adopters.</p>\n<p>Fast forward one year, React has strongly <a href=\"https://news.ycombinator.com/item?id=7489959\">grown in popularity</a>. Special acknowledgments go to Khan Academy, the ClojureScript community, and established frameworks such as Ember and Angular for contributing to and debating on our work. We’d also like to thank all the <a href=\"https://github.com/facebook/react/graphs/contributors\">individual contributors</a> who have taken the time to help out over the past year. React, as a library and as a new paradigm on the web, wouldn’t have gained as much traction without them. In the future, we will continue to try to set an example of what’s possible to achieve when we rethink about current “best practices”.</p>\n<p>Here’s to another year!</p>","excerpt":"Today marks the one-year open-source anniversary of React. It’s been a crazy ride. 2.3k commits and 1.5k issues and pull requests later, we’re approaching version 1.0 and nearing 7k GitHub stars, with big names such as Khan Academy, New York Times, and Airbnb (and naturally, Facebook and Instagram) using React in production, and many more developers blogging their success stories with it. The  roadmap  gives a glimpse into the future of the library; exciting stuff lies ahead! Every success has…","frontmatter":{"title":"One Year of Open-Source React","next":null,"prev":null,"author":[{"frontmatter":{"name":"Cheng Lou","url":"https://twitter.com/_chenglou"}}]},"fields":{"date":"May 28, 2014","path":"blog/2014-05-29-one-year-of-open-source-react.md","slug":"/blog/2014/05/29/one-year-of-open-source-react.html"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"React v16.3.0: New lifecycles and context API"},"fields":{"slug":"/blog/2018/03/29/react-v-16-3.html"}}},{"node":{"frontmatter":{"title":"Update on Async Rendering"},"fields":{"slug":"/blog/2018/03/27/update-on-async-rendering.html"}}},{"node":{"frontmatter":{"title":"Sneak Peek: Beyond React 16"},"fields":{"slug":"/blog/2018/03/01/sneak-peek-beyond-react-16.html"}}},{"node":{"frontmatter":{"title":"Behind the Scenes: Improving the Repository Infrastructure"},"fields":{"slug":"/blog/2017/12/15/improving-the-repository-infrastructure.html"}}},{"node":{"frontmatter":{"title":"Introducing the React RFC Process"},"fields":{"slug":"/blog/2017/12/07/introducing-the-react-rfc-process.html"}}},{"node":{"frontmatter":{"title":"React v16.2.0: Improved Support for Fragments"},"fields":{"slug":"/blog/2017/11/28/react-v16.2.0-fragment-support.html"}}},{"node":{"frontmatter":{"title":"React v16.0"},"fields":{"slug":"/blog/2017/09/26/react-v16.0.html"}}},{"node":{"frontmatter":{"title":"React v15.6.2"},"fields":{"slug":"/blog/2017/09/25/react-v15.6.2.html"}}},{"node":{"frontmatter":{"title":"DOM Attributes in React 16"},"fields":{"slug":"/blog/2017/09/08/dom-attributes-in-react-16.html"}}},{"node":{"frontmatter":{"title":"Error Handling in React 16"},"fields":{"slug":"/blog/2017/07/26/error-handling-in-react-16.html"}}}]}},"pathContext":{"slug":"/blog/2014/05/29/one-year-of-open-source-react.html"}}

